'use client';

import { useEffect } from 'react';

const BLANK_ICON_DATA_URL = "data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3crect width='16' height='16' fill='%233b82f6'/%3e%3c/svg%3e";

export function TitleBarCleaner() {
  useEffect(() => {
    const originalTitle = document.title;
    const trimmed = originalTitle.trim();

    if (trimmed.length > 0) {
      document.title = ' ';
    }

    const iconSelectors = "link[rel='icon'], link[rel='shortcut icon']";
    const links = Array.from(document.head.querySelectorAll<HTMLLinkElement>(iconSelectors));
    const stored = links.map((link) => ({
      element: link,
      href: link.getAttribute('href') ?? '',
      rel: link.getAttribute('rel') ?? '',
      type: link.getAttribute('type') ?? '',
    }));

    if (links.length === 0) {
      const link = document.createElement('link');
      link.rel = 'icon';
      document.head.appendChild(link);
      links.push(link);
      stored.push({ element: link, href: '', rel: 'icon', type: '' });
    }

    links.forEach((link) => {
      link.setAttribute('href', BLANK_ICON_DATA_URL);
      link.setAttribute('type', 'image/svg+xml');
      link.setAttribute('rel', 'icon');
    });

    const applicationNameMeta = document.head.querySelector<HTMLMetaElement>("meta[name='application-name']");
    const savedAppName = applicationNameMeta?.getAttribute('content') ?? null;
    let createdMeta: HTMLMetaElement | null = null;

    if (applicationNameMeta) {
      applicationNameMeta.setAttribute('content', ' ');
    } else {
      const meta = document.createElement('meta');
      meta.setAttribute('name', 'application-name');
      meta.setAttribute('content', ' ');
      document.head.appendChild(meta);
      createdMeta = meta;
    }

    return () => {
      document.title = originalTitle;

      stored.forEach(({ element, href, rel, type }) => {
        if (href) {
          element.setAttribute('href', href);
        } else {
          element.removeAttribute('href');
        }

        if (rel) {
          element.setAttribute('rel', rel);
        } else {
          element.removeAttribute('rel');
        }

        if (type) {
          element.setAttribute('type', type);
        } else {
          element.removeAttribute('type');
        }
      });

      if (createdMeta) {
        createdMeta.remove();
      } else if (applicationNameMeta) {
        if (savedAppName !== null) {
          applicationNameMeta.setAttribute('content', savedAppName);
        } else {
          applicationNameMeta.removeAttribute('content');
        }
      }
    };
  }, []);

  return null;
}
